











import Vue from "vue";
export default Vue.extend({
  name: "crm-input",
  props: {
    value: {
      type: String,
      default: "",
    },
    placeholder: {
      type: String,
      default: "",
    },
    id: {
      type: String,
      default: "",
    },
    className: {
      type: String,
      default: "",
    },
  },
  methods: {
    onHandleInput(value: string) {
      this.$emit("input", value);
    },
  },
});
